<template>
  <div class="PixelMaster">
    <div class="status-header">
      <div>
      <span> Round: {{ round + 1 }} </span>•
      <span> Pixels drawn: {{ info.pixel_drawn }} </span>•
      <span> Pot: {{ new Number(info.pool_balance.toNumber()).toFixed(3) }} key </span>•
      <span> Last painter: {{ info.last_painter.slice(0, 6) }} </span>
      <span v-if="hover_pixel != -1">• {{`(${hover_pixel%info.n_row}, ${~~(hover_pixel/info.n_row)})`}} </span>
      <span v-if="hover_pixel != -1">• Price: {{ new Number(img_price[hover_pixel] || 1).toFixed(4) }} </span>
      <span v-if="expected_cost != 0">• Expected Cost: {{ new Number(expected_cost || 0).toFixed(4) }} </span>
      </div>
    </div>
    <div class="main-container">
      <div class="middle-container">
        <div class="card">
          <div class="card-body card-shadow" ref="gameCanvas" style="padding: 16px">
            <svg @drag.prevent="" style="margin: 0 auto; display: block" ref="canvas" :width="(width + 0.2) * img.n" :height="(width + 0.2) * img.n">
              <g v-for="(_, i) in info.n_col" :key="i" :transform="`translate(${i * (width + 0.2)}, 0)`">
                <g v-for="(_, j) in info.n_row" :key="j" :transform="`translate(0, ${j * (width + 0.2)})`">
                  <rect :width="width - 0.2" :height="width - 0.2" class="pixel"
                    :class="{ pending: img_pending[i + j * info.n_row]}"
                    :fill="img_temp_color[i + j * info.n_row] || img_color[i + j * info.n_row]"
                    @mouseover="pixelMouseover(i, j)"
                    @mouseout="pixelMouseout(i, j)"
                    @click="pixelClick(i, j)"
                  />
                </g>
              </g>
            </svg>
          </div>
          <div class="toolbar">
            <div class="card-shadow toolbar1">
              <div class="button-wrapper">
                <button @click="color_picker_show = !color_picker_show">
                  <font-awesome-icon icon="pen"/>
                  <span :style="{ background: `${color_selected} none repeat scroll 0% 0%` }" class="PaintToolbar_colorIndicator"/>
                </button>
                <div class="PaintToolbar_colorPicker PaintToolbar_fourBit" v-show="color_picker_show">
                  <div class="ColorPicker_container">
                    <ol class="ColorPicker_fourBitColorBlocks">
                      <li v-for="(color, index) in color_candidate" :key="color" :title="color"
                        :class="{ disabled: index >= color_candidate_available }"
                        :style="{ background: `${color} none repeat scroll 0% 0%` }" class="ColorPicker_fourBitColorBlock"
                        @click="colorSelect(color)"/>
                    </ol>
                  </div>
                </div>
              </div>
              <div class="button-wrapper">
                <button @click="toolbarUndo" :disabled="pixel_task.length == 0 || is_watch_mode">
                  <font-awesome-icon icon="reply"/>
                </button>
              </div>
              <div class="button-wrapper">
                <button @click="toolbarRedo" :disabled="pixel_task_backup.length == 0 || is_watch_mode">
                  <font-awesome-icon icon="share"/>
                </button>
              </div>
              <div class="button-wrapper">
                <button @click="toolbarShot">
                    <a class="download" download="pixelmaster"><img src="../assets/screen.png"/></a>
                </button>
              </div>
            </div>
            <div class="toolbar2">
                <button @click="toolbarSave" :disabled="pixel_task.length == 0 || me.team == 0 || is_watch_mode">
                  <img src="../assets/go.png" style="width: 100%"/>
                </button>
            </div>
          </div>
          <div class="result card-shadow">
            <div v-if="me.team != 0" class="button-wrapper">
                {{ `Current infer result is ${info.current_infer_result}, your team will ` +
                    (expected_state ? 'win' : 'fail') + ` in this game.` }}
            </div>
            <div v-else-if="is_watch_mode" class="button-wrapper">
                {{ `Current infer result is ${info.current_infer_result}.` }}
            </div>
            <div v-else class="button-wrapper">
                You havn't join a blue/red team shown at right panel.
            </div>
          </div>
        </div>
      </div>
      <div class="right-container">
        <div class="card">
          <div class="card-body card-shadow">
              <div class="card-line"><h3>INFO</h3></div>
              <div class="card-line card-bg card-bg1">
                <span class="m1 heavytext">
                  Total Pixel Drawn
                  <span class="tool-tip">As the accumulated number of pixel changing reaches 10000, the DigitalCrash Contract freezes the Canvas and begins to settle</span>
                </span>
                <span class="m2 progress">                  
                  <div class="progress-bar" :style="{width: info.pixel_drawn / info.tx_limit * 100 + '%'}"></div>
                  <span style="position: relative;top: -1px"> {{ info.pixel_drawn }} / {{ info.tx_limit }} </span>
                </span>
              </div>
              <div class="card-line card-bg">
                <span class="m1 heavytext"  id="recharge_span" for="payment-input">Recharge </span>
                <input class="m1 form-control" id="payment-input" v-model="payment_value" type="number" min="0"/>
                <span class="card-key">key<span class="tool-tip">1 key = 1 CTXC</span></span>
                <button type="button" :disabled="is_watch_mode" @click="clickPay" class="btn btn-sm card-btn">
                    Pay
                </button>
              </div>
              <div class="card-line card-bg">
                <span class="m1 heavytext" id="balance_span">Balance</span>
                <span class="m1 heavytext" style="text-align: center">{{ personal_balance }}</span>
                <span class="card-key heavytext">key<span class="tool-tip">1 key = 1 CTXC</span></span>
                <button type="button" :disabled="is_watch_mode" @click="clickWithdraw" class="btn btn-sm card-btn heavytext">
                    Withdraw
                </button>
              </div>
              <div class="card-line card-bg">
                <span class="m1 heavytext" id="stake_span">Stake</span>
                <span class="m1 heavytext" style="text-align: center">{{ personal_stake }}</span>
                <span class="card-key heavytext">key<span class="tool-tip">1 key = 1 CTXC</span></span>
                <span class="card-btn"></span>
              </div>
              <div class="card-line card-bg">
                <span class="m1 heavytext" id="expected_return_span">Expected Return<span class="tool-tip">if your team win the game</span></span>
                <span class="m1 heavytext" style="text-align: center">{{ expected_return }}</span>
                <span class="card-key heavytext">key<span class="tool-tip">1 key = 1 CTXC</span></span>
                <span class="card-btn heavytext"></span>
              </div>
              <div class="card-line">
                  <h3>TEAM</h3>
              </div>
              <div class="card-line pk">
                <div class="red" :style="{
                  width: (info.number_of_pixel[0] + info.number_of_pixel[1]) == 0 ? 50 : (
                    info.number_of_pixel[0] / (info.number_of_pixel[0] + info.number_of_pixel[1]) * 100) + '%'}"></div>
              </div>
              <div class="card-line" style="display: flex;flex-wrap: wrap;justify-content: space-between;align-items: center;margin: 0 8px 20px">
                <button :disabled="me.team != 0 && me.team != 1 || is_watch_mode" @click="joinTeam(1)" type="button" class="btn btn-team btn-sm">
                  <img src="../assets/robot1.png"/>
                  <span class="tool-tip">win when the final infer result is 1, 3, 5, 7, 9</span>
                </button>
                <img src="../assets/fight.png"/>
                <button :disabled="me.team != 0 && me.team != 2 || is_watch_mode" @click="joinTeam(2)" type="button" class="btn btn-team btn-sm">
                  <img src="../assets/robot2.png"/>
                  <span class="tool-tip">win when the final infer result is 0, 2, 4, 6, 8</span>
                </button>
              </div>
              <div class="card-line">
                <h3 style="margin-bottom: 20px">ACTION</h3>
                <div class="card-bg">
                    <span class="ColorPicker_fourBitColorBlock" v-for="(d, index) in pixel_task" :key="index"
                        v-tooltip="`(${d[0]},${d[1]})`"
                        :style="{
                          background: `${d[2]} none repeat scroll 0% 0%`,
                        }">
                    </span>
                </div>
              </div>
              <div class="card-line" v-if="own_address == contract_owner">
                <button
                @click="onContractReset()"
                type="button" class="btn btn-sm btn-label heavytext">
                    Reset
                </button>
              </div>
          </div>
        </div>
      </div>
    </div>

    <div v-if="show_wallet">
      <div style="position: fixed;top: 0;bottom: 0;left: 0;right: 0;background: rgba(0,0,0,0.4);z-index: 50"></div>
      <div class="wallet">
        <h2 style="margin-top: 10px;margin-bottom: 30px">Tips</h2>
        <h4 v-if="is_wallet_login" style="font-size: 16px">Please make sure you have <a href="https://github.com/CortexFoundation/Cortex_Release">installed</a> and logged in to our CortexWallet!</h4>
        <h4 v-else style="font-size: 16px">Please make sure you have logged in to our CortexWallet!</h4>
        <button v-if="is_wallet_login" @click="show_wallet=false" style="font-size: 16px">Continue</button>
      </div>
    </div>

    <div class="loading" v-if="if_loading">
        <img src="../assets/loading.gif" width=50 height=50/>
        <br>
        <span v-if="gif_url=='pay'">Paying...</span>
        <span v-if="gif_url=='withdraw'">Withdrawing...</span>
        <span v-if="gif_url=='pending'">Pending...</span>
    </div>

    <div class="win" v-if="if_game_ending">
        <div class="win-container">
            <span class="close" @click="if_game_ending=false"><font-awesome-icon icon="times"/></span>
            <div v-if="win_team == 1">
                <img src="../assets/win-1.png"/>
                <div class="reward reward1">{{win_return>0?"+" : (win_return==0?"-":"")}} {{win_return}}</div>
            </div>
            <div v-if="win_team == 2">
                <img src="../assets/win-2.png"/>
                <div class="reward reward2">{{win_return>0?"+" : (win_return==0?"-":"")}} {{win_return}}</div>
            </div>
        </div>
    </div>

    <template>
      <notify-me :event-bus="bus">
          <template slot="content" scope="{data}">
              <div style="width: 100%; word-break: break-all; text-align: left">
                  <h4><b>{{ data.title }}</b></h4>
                  <p style="margin: 0">{{ data.text }}</p>
              </div>
          </template>
      </notify-me>
    </template>
  </div>
</template>

<script>
/*
1. Banner bar
2. Global status bar / total value / pixels drawn / pot / last painter(Date)
3. Private status bar / accumulated stake / team / recent actions
4. Toolbar
5. Infer result (infer historical result) / pixel_drawn / max pixel_drawn
6. Canvas / 28 * 28
*/
import Vue from 'vue';
import Web3 from "web3";
import jsonInterface from "../contract/jsoninterface.js";
import Notify from 'vue-notify-me';
import html2canvas from 'html2canvas';
const contract_address = "0xa74e020398c6661bdb2dd5188df961a6afcd4ab1";
// "0x892b863d1f61c6b34318c6fbaa7049d91d1d022a";
// "0xf253dd2827d515a75678e2454ce0d905b65d900d";
const BN = Web3.utils.BN;
/* eslint-disable */

function sleep(time) {
  return new Promise(resolve => setTimeout(resolve, time));
}

export default {
  name: "PixelMaster",
  data() {
    return {
      img: generateBinaryImage(),
      width: 15,
      hover_pixel: -1,
      current_pixel: null,
      payment_value: 0,
      contract: null,
	    ro_contract: null,
      web3: null,
      ro_web3: null,
      bus: new Vue(),
      color_candidate: [
        "#FFFFFF",
        "#7F7F7F",
        "#000000",
      ],
      contract_owner: "0xe50e4d03d6edda6abda52e3cdf6c9752d97d2242",
      color_selected: "#222222",
      color_candidate_available: 4,
      show_wallet: false,
      color_picker_show: false,

      pixel_task: [],
      pixel_task_backup: [],

      default_gturing_price: new BN("1000000000000000000"),

      info: {
        pixel_drawn: 0,
        tx_limit: 0,
        pool_balance: new BN(0),
        width: 28,
        height: 28,
        last_painter: "0x",
        current_infer_result: 0,
        channel: 1,
        growth_factor: 0,
        reimburse_factor: 0,
        fee_rate: 0,
        pool_factor: 0,        
        number_of_pixel: [100, 100],
        number_of_player: [],
        total_stake: new BN(1000),
      },
      round: 0,

      me: {
        address: "",
        stake: new BN(0),
        team_stake: new BN(1000),
        balance: new BN(0),
        team: 0,
      },

      img_data: [],
      img_price: [],
      img_color: [],
      img_pending: [],
      img_temp_color: [],
      img_nbytes: 0,
      refreshing: false,

	    own_address: "",

      if_loading: false,
      if_game_ending: false,
      is_initialize: true,
      is_watch_mode: true,
      is_wallet_login: true,
      win_team: 1,
      win_return: 0,
      gif_url: null,
    };
  },
  watch: {
    pixel_task(val) {
      this.img_temp_color = [];
      for (let i = 0; i < val.length; ++i) {
        const index = val[i][0] + val[i][1] * this.info.n_row;
        this.img_temp_color[index] = val[i][2];
      }
    },
  },
  async mounted() {
    if (typeof ctxWeb3 == "undefined") {
      this.show_wallet = true;
    }
    
    let counter = 0;
    while (typeof ctxWeb3 == "undefined") {
      await sleep(50);
      ++counter;
      if (counter > 10) {
        break;
      }
    }

    if (typeof ctxWeb3 == "undefined") {
      const another_provider = new Web3.providers.HttpProvider("https://cerebro.cortexlabs.ai/wallet");
      this.web3 = new Web3(another_provider);
      const ro_web3 = new Web3(another_provider);
      this.contract = new this.web3.eth.Contract(jsonInterface, contract_address);
      this.ro_contract = new ro_web3.eth.Contract(jsonInterface, contract_address);
      this.own_address = null;
    } else {
      this.is_watch_mode = false;
      this.is_wallet_login = false;

      if (!ctxWeb3.eth.defaultAccount) {
        this.show_wallet = true;
      }

      while (!ctxWeb3.eth.defaultAccount) {
        await sleep(50);
      }

      this.is_wallet_login = true;
      this.show_wallet = false;

      const another_provider = new Web3.providers.HttpProvider("https://cerebro.cortexlabs.ai/wallet");
      this.web3 = new Web3(ctxWeb3.currentProvider);
      const ro_web3 = new Web3(another_provider);
      this.contract = new this.web3.eth.Contract(jsonInterface, contract_address);
      this.ro_contract = new ro_web3.eth.Contract(jsonInterface, contract_address);
      this.own_address = ctxWeb3.eth.defaultAccount;

      console.log("Init ctxWeb3 successfully.");
    }
    this.initialVariable();
    setInterval(() => this.refreshVariable(), 3000);
  },
  components: {
    'notify-me': Notify
  },
  computed: {
    expected_return() {
      try {
        const value = this.me.stake
          .mul(this.info.pool_balance)
          .mul(new BN(1000))
          .div(this.me.team_stake)
          .toNumber();
        return value / 1000;
      } catch (e) {
        return 0;
      }
    },
    personal_balance() {
      try {
        const value = this.me.balance.mul(new BN(1000)).div(this.default_gturing_price).toNumber();
        return value / 1000;
      } catch (e) {
        return 0;
      }
    },
    personal_stake() {
      try {
        const value = this.me.stake.mul(new BN(1000)).div(this.default_gturing_price).toNumber();
        return value / 1000;
      } catch (e) {
        return 0;
      }
    },
    expected_state() {
      try {
        const res_team = 2 - (this.info.current_infer_result & 1);
        return res_team == this.me.team;
      } catch (e) {
        return 0;
      }
    },
    expected_cost() {
      try {
        let ret = 0;
        for (let i = 0; i < this.pixel_task.length; ++i) {
          let idx = this.pixel_task[i][0] + this.pixel_task[i][1] * this.info.n_row;
          ret += this.img_price[idx];
        }
        return ret;
      } catch (e) {
        return 0;
      }
    },
  },
  methods: {
    getPersonalBalance() {
      try {
        const value = this.me.balance.mul(new BN(1000)).div(this.default_gturing_price).toNumber();
        return value / 1000;
      } catch (e) {
        return 0;
      }
    },
    getPersonalStake() {
      try {
        const value = this.me.stake.mul(new BN(1000)).div(this.default_gturing_price).toNumber();
        return value / 1000;
      } catch (e) {
        return 0;
      }
    },
    updateImgData() {
      for (let i = 0; i < this.img_data.length; ++i) {
        const v = 255 - this.img_data[i];
        this.img_color[i] = `rgb(${v},${v},${v})`;
      }
    },
    toolbarUndo() {
      let n = this.pixel_task.length;
      this.pixel_task_backup.push(this.pixel_task.splice(n - 1, 1)[0]);
    },
    toolbarRedo() {
      let n = this.pixel_task_backup.length;
      this.pixel_task.push(this.pixel_task_backup.splice(n - 1, 1)[0]);
    },
    toolbarShot() {
      html2canvas(document.body).then(function(canvas){
        var url = canvas.toDataURL();
        download(url);
      })
    },
    initialVariable() {
      this.ro_contract.methods
        .getConstant()
        .call({ from: this.own_address })
        .then(d => {
          this.info.n_row = +d[0];
          this.info.n_col = +d[1];
          this.info.n_ch = +d[2];
          this.info.tx_limit = +d[3];
          this.info.growth_factor = +d[4];
          this.info.reimburse_factor = +d[5];
          this.info.pool_factor = +d[6];
          this.default_gturing_price = new BN(d[7]);
          this.img_nbytes = this.info.n_row * this.info.n_col * this.info.n_ch;
          this.img_data = [];
          for (let i = 0; i < this.img_nbytes; ++i) {
            this.img_data.push(0);
          }
          this.updateImgData();
        });
    },
    onContractReset() {
      this.contract.methods
        .reset()
        .send({ from: this.own_address })
        .then(d => {
          alert("contract reset finish");
        });
    },
    async refreshVariable() {
      if (this.refreshing) {
        return;
      } else {
        this.refreshing = true;
      }

      let d;
      
      let data = await this.ro_contract.methods
        .img()
        .call({ from: this.own_address });

      this.img_data = [];
      for (let i = 2; i < data.length; i += 2) {
        this.img_data.push(parseInt(data.substring(i, i + 2), 16));
      }
      this.updateImgData();

      d = await this.ro_contract.methods
        .getInfo()
        .call({ from: this.own_address });

      const last_infer_result = this.info.current_infer_result;
      const personal_team = this.me.team;
      const last_expected_return = this.expected_return;

      this.info.pixel_drawn = +d[0];
      this.info.number_of_player = [+d[1], +d[2]];
      this.info.pool_balance = (new BN(d[3])).div(this.default_gturing_price);
      this.info.current_infer_result = +d[4];
      this.info.last_painter = d[5];

      d = await this.ro_contract.methods
        .round()
        .call({ from: this.own_address });

      const last_round = this.round;
      this.round = +d;

      d = await this.ro_contract.methods
        .getTeamTotalStake()
        .call({ from: this.own_address });
      const stake1 = new BN(d[0]);
      const stake2 = new BN(d[1]);
      this.number_of_pixel = [stake1.div(this.default_gturing_price).toNumber(), stake2.div(this.default_gturing_price).toNumber()];
      this.info.total_stake = stake1.add(stake2);
      if (this.me.team == 1) {
        this.me.team_stake = stake1;
      } else if (this.me.team == 2) {
        this.me.team_stake = stake2;
      } else {
        this.me.team_stake = new BN(0);
      }
      
      d = await this.ro_contract.methods
        .getPersonalInfo()
        .call({ from: this.own_address });

      const last_balance = this.personal_balance;
      const last_stake = this.personal_stake;

      this.me.balance = new BN(d[0]);
      this.me.stake = new BN(d[1]);

      if (this.round != last_round && !this.is_initialize) {
        const current_balance = this.getPersonalBalance();
        const current_stake = this.getPersonalStake();
        if (last_infer_result % 2 == 1) {
          this.win_team = 1;
        } else {
          this.win_team = 2;
        }
        if (this.win_team == personal_team) {
          this.win_return = last_expected_return;
        } else {
          this.win_return = -last_stake;
        }
        this.if_game_ending = true;
      }
      this.me.team = +d[2];

      this.adjustCanvas();

      data = await this.ro_contract.methods
        .txCount()
        .call({ from: this.own_address });

      this.img_price = [];
      for (let i = 2; i < data.length; i += 2) {
        let tx_count = parseInt(data.substring(i, i + 2), 16);
        let factor = this.info.growth_factor.valueOf() / 100;
        this.img_price.push(Math.pow(factor, tx_count));
      };

      this.if_loading = false;
      this.refreshing = false;
      this.is_initialize = false;
    },
    adjustCanvas() {
      if (!this.$refs.gameCanvas) return;
      const bounding = this.$refs.gameCanvas.getBoundingClientRect();
      //const width = Math.min(bounding.width, bounding.height);
      const width = bounding.width;
      this.width = (width - 40) / this.img.n - 0.2;
      return (width - 40) / this.img.n - 0.2;
    },
    joinTeam(i) {
      if (i == this.me.team) return;
      this.if_loading = true;
      this.contract.methods
        .selectTeam(this.round, i)
        .send({ from: this.own_address })
        .then((receipt) => {
          try {
            this.if_loading = false;
          } catch (err) {
            this.bus.$emit('notify-me', {
              status: 'is-danger',
              data: {
                  title: 'error',
                  text: err,
              }
            });
          }
        });
    },
    clickPay() {
      this.if_loading = true;
      this.gif_url = "pay";
      this.contract.methods
        .pay()
        .send({
          from: this.own_address,
          value: new BN(this.payment_value).mul(this.default_gturing_price)
        })
        .then(receipt => {
          try {
            this.if_loading = false;
          } catch (err) {
            this.bus.$emit('notify-me', {
              status: 'is-danger',
              data: {
                  title: 'error',
                  text: err,
              }
            });
          }
        });
    },
    clickWithdraw() {
      this.if_loading = true;
      this.gif_url = "withdraw";
      this.contract.methods
        .withdraw(
          "0x" + this.me.balance.toJSON()
        )
        .send({ from: this.own_address })
        .then(d => {
          try {
            this.if_loading = false;
          } catch (err) {
            this.bus.$emit('notify-me', {
              status: 'is-danger',
              data: {
                  title: 'error',
                  text: err,
              }
            });
          }
        });
    },
    colorSelect(color) {
      const index = this.color_candidate.indexOf(color);
      if (index == -1 || index >= this.color_candidate_available) {
        this.color_picker_show = false;
        return;
      }
      this.color_selected = color;
      this.color_picker_show = false;
    },
    pixelMouseover(i, j) {
      this.hover_pixel = i + j * this.info.n_row;
    },
    pixelMouseout(i, j) {
      if (this.hover_pixel == i + j * this.info.n_row) {
        this.hover_pixel = -1;
      }
    },
    lowBalanceWarning() {
      this.pixel_task.pop();
      this.bus.$emit('notify-me', {
        status: 'is-danger',
        data: {
            title: 'error',
            text: `Estimated cost will exceed your balance`,
        }
      });
    },
    pixelClick(i, j) {
      if (this.is_watch_mode) {
        this.bus.$emit('notify-me', {
          status: 'is-danger',
          data: {
              title: 'error',
              text: `The watch mode does not allow any pixel operation.`,
          }
        });
        return;
      }
      if (this.img_pending[i + j * this.info.n_row]) {
        this.bus.$emit('notify-me', {
          status: 'is-danger',
          data: {
              title: 'error',
              text: `Drawing task on this pixel is still pending`,
          }
        });
        return;
      }
      if (this.img_temp_color[i + j * this.info.n_row]) {
        this.img_temp_color[i + j * this.info.n_row] = this.color_selected;
        for (let k = 0; k < this.pixel_task.length; ++k) {
          if (this.pixel_task[k][0] == i && this.pixel_task[k][1] == j) {
            this.pixel_task.splice(k, 1);
            break;
          }
        }
      }
      this.pixel_task.push([i, j, this.color_selected]);
      if (this.pixel_task.length > 10) {
        this.pixel_task.pop();
        this.bus.$emit('notify-me', {
          status: 'is-danger',
          data: {
              title: 'error',
              text: `you cannot modify more than 10 pixels in one operation`,
          }
        });
        return;
      }
      if (this.expected_cost > this.personal_balance) {
        this.lowBalanceWarning();
        return;
      }
      this.pixel_task_backup = [];
    },
    async toolbarSave() {
      this.if_loading = true;
      this.gif_url = "pending";
      let ops = [];

      const tmp_task = [];
      for (let i = 0; i < this.pixel_task.length; ++i) {
        ops.push(this.pixel_task[i][0]);
        ops.push(this.pixel_task[i][1]);
        let color = 255 - parseInt(this.pixel_task[i][2].substring(1, 3), 16);
        ops.push(color);
        tmp_task.push(this.pixel_task[i]);
        const index = this.pixel_task[i][0] + this.pixel_task[i][1] * this.info.n_row;
        this.img_pending[index] = 1;
      }
      this.pixel_task = [];

      try {
        const d = await this.contract.methods
          .draw(this.round, ops)
          .send({ from: this.own_address });
      } catch (err) {
        this.bus.$emit('notify-me', {
          status: 'is-danger',
          data: {
              title: 'error',
              text: 'Transaction cancelled.',
          }
        });
      }
      for (let i = 0; i < tmp_task.length; ++i) {
        const index = tmp_task[i][0] + tmp_task[i][1] * this.info.n_row;
        this.img_pending[index] = 0;
      }
    }
  },
  props: {
    msg: String
  }
};

function generateBinaryPixel() {
  return {
    data: {
      msg: "no message",
      price: 1,
      color: 0

    },
    hover: false
  };
}

function generateBinaryImage(n) {
  n = n || 28;
  const img = [];
  for (let i = 0; i < n; ++i) {
    const row = [];
    for (let j = 0; j < n; ++j) {
      let pixel = generateBinaryPixel();
      pixel.position = [i, j];
      row.push(pixel);
    }
    img.push(row);
  }
  return {
    data: img,
    n: n
  };
}

function download(url) {
    var el = document.getElementsByClassName('download')[0];
    el.setAttribute('href', url)
}
